import React, { useRef, useEffect } from 'react';
import ArrowImg from '../img/arrow.svg'
import { Helmet } from 'react-helmet-async';
import CertificateImg from '../img/certificate.jpg'


function Certificate() {


    const initialLoad = useRef(true);

    const scrollToTop = () => {
        window.scrollTo({
            top: 0,
            behavior: "smooth"
        });
    };

    useEffect(() => {
        if (initialLoad.current) {
            scrollToTop();
            initialLoad.current = false;
        }
    }, []);

  return (
    <div>
    <Helmet>
        <title>  IABC</title>
        <meta
        name="description"
        content=' "    "        ,             '
        />
        <link rel="canonical" href="/certificate"/>
      </Helmet>
    <div className="documents-wrapper mt">
    <div className="text-center">
        <div className="page-title"></div>
        <div className="awards-title">      </div>
        </div>
        <div className='certificate-img'><img src={CertificateImg} alt="Certificate" /></div>
        <p className="page-text"> "    "        ,              ,     .      :</p>
        <ul className="content-details-list page-text">
          <li>  <span class="arrow-icon-wrapper">
          <img src={ArrowImg} className="my-icon-style" alt="ArrowImg" />
            </span>       ,     </li>
            <li>  <span class="arrow-icon-wrapper">
          <img src={ArrowImg} className="my-icon-style" alt="ArrowImg" />
            </span>       </li>
            <li>  <span class="arrow-icon-wrapper">
          <img src={ArrowImg} className="my-icon-style" alt="ArrowImg" />
            </span>   ,    ,        </li>
            <li>  <span class="arrow-icon-wrapper">
          <img src={ArrowImg} className="my-icon-style" alt="ArrowImg" />
            </span>      ,    ,     </li>
            <li>  <span class="arrow-icon-wrapper">
          <img src={ArrowImg} className="my-icon-style" alt="ArrowImg" />
            </span>        ,     </li>
        </ul>
        <p className="page-text">   "    "            ,  , , , , , , , , , ,   .                .</p>
        <p className="page-text">             .  ,         ,      ,         .</p>
        <p className="page-text">           ,   ,    ,  ,      .</p>
        <p className="page-text">     ,        ,          .</p>
        <p className="page-text"> "    "     ,             ,            .</p>
    </div>
    </div>
  );
}

export default Certificate;
