import React, { useRef, useEffect } from 'react';
import { Helmet } from 'react-helmet-async';


function StatutePage() {


    const initialLoad = useRef(true);

    const scrollToTop = () => {
        window.scrollTo({
            top: 0,
            behavior: "smooth"
        });
    };

    useEffect(() => {
        if (initialLoad.current) {
            scrollToTop();
            initialLoad.current = false;
        }
    }, []);

  return (
    <div>
    <Helmet>
        <title>       </title>
        <meta
        name="description"
        content="                 "
        />
        <link rel="canonical" href="/statutepage"/>
      </Helmet>
    <div className="documents-wrapper mt">
        <div className="text-center">
        <div className="page-title"></div>
        <div className="awards-title">       </div>
        </div>
        <p className="page-text">                </p>
        <p className="page-subtext">         :</p>
        <ul class="custom-list">
          <li>    ;</li>
          <li>         ;</li>
          <li>                 ;</li>
          <li>     -  .</li>
        </ul>
        <p className="page-subtext">          :</p>
        <ul class="custom-list">
          <li>   </li>
          <li>   </li>
          <li>     </li>
          <li>    </li>
          <li>     </li>
        </ul>
        <p className="page-text">          ,    .</p>
        <p className="page-text">              ,   , -        .</p>
        <p className="page-text">                  .             .</p>
        <p className="page-text">                 .</p>
        <p className="page-text">                 <b>    .</b></p>
        <p className="page-text">                ,     <b>    </b>    .</p>
        <p className="page-text">                ,        ,       .</p>
        <p className="page-text">               ,    ,       .</p>
        <p className="page-text">          ,     <b>    :</b></p>
        <ul class="custom-list">
          <li>         ;</li>
          <li>   ;</li>
          <li>              ;</li>
          <li>                   </li>

        </ul>
        <p className="page-text"></p>
        <p className="page-text"></p>
    </div>
    </div>
  );
}

export default StatutePage;
