import React, { useRef, useEffect, useState } from 'react';
import ArrowImg from '../img/arrow.svg'
import { Helmet } from 'react-helmet-async';
import { Link } from 'react-router-dom';
import certificate_members from './certificate_members';


function Awards() {

  const membersPerPage = 10;
  const [currentPage, setCurrentPage] = useState(1);
  //    
  const [animating, setAnimating] = useState(false);

  const indexOfLastMember = currentPage * membersPerPage;
  const indexOfFirstMember = indexOfLastMember - membersPerPage;
  const currentMembers = certificate_members.slice(indexOfFirstMember, indexOfLastMember);

  const totalPages = Math.ceil(certificate_members.length / membersPerPage);

  const paginate = (pageNumber) => {
    if (pageNumber < 1 || pageNumber > totalPages || animating) return;
    setAnimating(true); //  
    setCurrentPage(pageNumber);
    setTimeout(() => setAnimating(false), 300); //    300 
  };

    const certificateRef = useRef(null);
    const statuteRef = useRef(null);
    const certificatmRef = useRef(null);

    const scrollToertificate = () => {
      certificateRef.current.scrollIntoView({ behavior: 'smooth' });
    };

    const scrollToStatute = () => {
      statuteRef.current.scrollIntoView({ behavior: 'smooth' });
    };
    const scrollToertificatMember = () => {
      certificatmRef.current.scrollIntoView({ behavior: 'smooth' });
    };

    const initialLoad = useRef(true);

    const scrollToTop = () => {
        window.scrollTo({
            top: 0,
            behavior: "smooth"
        });
    };

    useEffect(() => {
        if (initialLoad.current) {
            scrollToTop();
            initialLoad.current = false;
        }
    }, []);

  return (
    <div>
    <Helmet>
        <title>  IABC</title>
        <meta
        name="description"
        content="      (International Association of Beauty and Care) IABC"
        />
        <link rel="canonical" href="/awards"/>
      </Helmet>
      <div class="awards_img">
        <div className="content-blur"></div>
    <div class="content-overlay">
    <h1 className="main-title">  IABC</h1>
    <h1 className="link-title" onClick={scrollToertificate}></h1>
    <h1 className="link-title" onClick={scrollToStatute}></h1>
    <h1 className="link-title" onClick={scrollToertificatMember}>  </h1>
    </div>
    </div>
    <div className="documents-wrapper">
    <div className="text-center">
        <div className="page-title" ref={certificateRef}></div>
        <div className="awards-title">      </div>
        </div>
        <p className="page-text"> "    "        ,              ,     .      :</p>
        <ul className="content-details-list page-text">
          <li>  <span class="arrow-icon-wrapper">
          <img src={ArrowImg} className="my-icon-style" alt="ArrowImg" />
            </span>       ,     </li>
            <li>  <span class="arrow-icon-wrapper">
          <img src={ArrowImg} className="my-icon-style" alt="ArrowImg" />
            </span>       </li>
            <li>  <span class="arrow-icon-wrapper">
          <img src={ArrowImg} className="my-icon-style" alt="ArrowImg" />
            </span>   ,    ,        </li>
            <li>  <span class="arrow-icon-wrapper">
          <img src={ArrowImg} className="my-icon-style" alt="ArrowImg" />
            </span>      ,    ,     </li>
            <li>  <span class="arrow-icon-wrapper">
          <img src={ArrowImg} className="my-icon-style" alt="ArrowImg" />
            </span>        ,     </li>
        </ul>
        <p className="page-text">   "    "            ,  , , , , , , , , , ,   .                .</p>
        <div className='content-details-button'>
        <Link to={{
          pathname: "/certificate",
          }}>
          <button className="main-content-button"></button>
          </Link>
        </div>
        <div className="text-center pt-100">
        <div className="page-title" ref={statuteRef}></div>
        <div className="awards-title">       </div>
        </div>
        <p className="page-text">                </p>
        <p className="page-subtext">         :</p>
        <ul class="custom-list">
          <li>    ;</li>
          <li>         ;</li>
          <li>                 ;</li>
          <li>     -  .</li>
        </ul>
        <p className="page-subtext">          :</p>
        <ul class="custom-list">
          <li>   </li>
          <li>   </li>
          <li>     </li>
          <li>    </li>
          <li>     </li>
        </ul>
        <div className='content-details-button'>
        <Link to={{
          pathname: "/statutepage",
          }}>
          <button className="main-content-button"></button>
          </Link>
        </div>
        <div className="text-center pt-100">
        <div className="page-title" ref={certificatmRef}>  </div>
        <div className="awards-title">    </div>
        <div className="table-responsive">
        <table className={`table ${animating ? 'table-animate-exit-active' : ''}`}>
          <thead>
            <tr>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
          {currentMembers.map((certificate_member, index) => (
                <tr key={index}>
                    <td>{certificate_member.name}</td>
                    <td>{certificate_member.country}</td>
                    <td>{certificate_member.achievements}</td>
                </tr>
            ))}
          </tbody>
        </table>
      </div>
      <div className="pagination-controls">
        <button
          onClick={() => paginate(currentPage - 1)}
          disabled={currentPage <= 1}
          aria-label="Previous"
        >
          &#10094;
        </button>
        <button
          onClick={() => paginate(currentPage + 1)}
          disabled={currentPage >= totalPages}
          aria-label="Next"
        >
          &#10095;
        </button>
      </div>
        </div>

    </div>
    </div>
  );
}

export default Awards;
